import React, { Component } from 'react';
import imcService from '../services/imcService';
import ResultExplanation from './ResultExplanation';
import './ImcCalculator.css';

class ImcCalculator extends Component {
    constructor(props) {
        super(props);
        this.state = {
            height: '',
            weight: '',
            displayedResult: null
        }
        this.onChange = this.onChange.bind(this);
        this.onSubmit = this.onSubmit.bind(this);
        this.clearDisplay = this.clearDisplay.bind(this);

    }

    onChange(event) {
        //grab the name using e.target
        this.setState({
            [event.target.name]: event.target.value

        });
    }
    onSubmit(event) {
        event.preventDefault();

        //  imcService.ImcCalculator(this.state.height, this.state.weight);
        //para o resultado da ImcCalculator() estar disponivel aqui, tem de se guardado:

        const res = imcService.imcCalculator(this.state.height, this.state.weight);
        const resultForDisplay = imcService.imcQualitativeEvaluation(res);

        this.setState({
            displayedResult: res,
            resToDisplay: resultForDisplay
        });
    }
    clearDisplay = () => {
        this.setState({
            height: '',
            weight: '',
            displayedResult: null,
            resToDisplay: ''
        })
    }


    render() {
        return (
            <div>
                <form className="form-horizontal inputForm" onSubmit={this.onSubmit} >
                    <div>
                        <label>Altura:</label><br />
                        <input name="height"
                            type="number"
                            step="0.01"
                            min='0'
                            max='2.50'
                            id="h"
                            placeholder="Insira a sua altura aqui. Ex.: 1,70"
                            value={this.state.height}
                            onChange={this.onChange} required />

                    </div>
                    <div>
                        <label>Peso:</label><br />
                        <input name="weight"
                            type="number"
                            step="0.01"
                            min='0'
                            max='300'
                            id="w"
                            placeholder="Insira o seu peso aqui. Ex.: 60"
                            onChange={this.onChange}
                            value={this.state.weight} required />
                    </div>
                    <br />
                </form>

                <div className="form-row text-center">
                    <div className="col-12 ">
                        <button className='btn btn-outline-primary mr-2 mt-2 mb-2'
                            type="submit"
                            id="reset-value" onClick={this.clearDisplay}>Reiniciar</button>
                        <button className='btn btn-outline-success  mr-2 mt-2 mb-2'
                            type="submit"
                            id="submit-weight" onClick={this.onSubmit}>Submeter</button>
                    </div>
                </div>
                <ResultExplanation
                    result={this.state.displayedResult}
                    resultExplanation={this.state.resToDisplay}
                />
                <div>
                </div>
            </div>

        )
    }
}
export default ImcCalculator;